<template>
  <el-container class="changepsw-el-container">
    <el-header class="changepsw-el-header">
      <el-page-header @back="goBack" content="修改密码"/>
    </el-header>
    <el-main class="changepsw-el-main">

      <el-form 
      status-icon 
      label-width="20px" 
      :model="changePasswordForm" 
      class="demo-ruleForm" 
      :rules="changePasswordFormRules">
        <!-- 原始密码 -->
        <tr>
          <td>原始密码:</td>
          <td>
            <div>
              <el-form-item prop="oldpassword">
                <el-input 
                v-model="changePasswordForm.oldpassword" 
                type="password" 
                placeholder="请输入原始密码" 
                show-password></el-input>
              </el-form-item>
            </div>
          </td>
        </tr>
        <!-- 新的密码 -->
        <tr>
          <td>新的密码:</td>
          <td>
            <el-form-item prop="newpassword">
              <el-input 
              v-model="changePasswordForm.newpassword"
               type="password"
                placeholder="请输入新的密码" 
                show-password></el-input>
            </el-form-item>
          </td>
        </tr>
        <!-- 确认密码 -->
        <tr>
          <td><span>确认密码:</span></td>
          <td>
            <el-form-item prop="checkpassword">
              <el-input 
              v-model="changePasswordForm.checkpassword" 
              type="password" 
              placeholder="请再次输入新的密码" 
              show-password></el-input>
            </el-form-item>
          </td>
        </tr>
      </el-form>
      <el-button round class="changepswInput" @click="updatePassword">提交修改</el-button>

    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'
export default {
  name: 'MainChangePassword',
  data() {
    return {
      // 表单的数据绑定对象
      changePasswordForm: {
        oldpassword: '',
        newpassword: '',
        checkpassword: ''
      },
      // 表单的验证规则
      changePasswordFormRules: {
          // 验证原始密码是否合法
          oldpassword: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
          ],
          // 验证新的密码是否合法
          newpassword: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
          ],
          // 验证确认密码是否合法
          checkpassword: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
          ]
      }

      }
    },
    methods: {
      goBack() {
        this.$router.go(-1)
      },
      updatePassword() {
        if(this.changePasswordForm.oldpassword === this.$store.state.currentUserMsg.password) {
          if(this.changePasswordForm.newpassword === this.changePasswordForm.checkpassword) {
            axios.put('http://localhost:3000/Register/' + this.$store.state.currentUserMsg.id, this.newPassword()).then(res => {
              this.$message({showClose: true, message: '密码修改成功, 请重新登录', type: 'success'})
              this.$store.commit('updatecurrentMsgPassword', this.changePasswordForm.newpassword)
              this.$router.push('/login')
            })
          }else {
            this.$message({showClose: true, message: '两次输入的密码不一致，请重新输入密码', type: 'error'})
          }
        }else {
          this.$message({showClose: true, message: '原始密码不正确', type: 'error'})
        }
      },
      newPassword() {
        let newUserMsg = {
          id: this.$store.state.currentUserMsg.id,
          username: this.$store.state.currentUserMsg.username,
          password: this.changePasswordForm.newpassword,
          power: this.$store.state.currentUserMsg.power
        }
        return newUserMsg
      }
    }
}
</script>

<style>
  .changepsw-el-main {
    margin: 0 32px;
  }
  .changepsw-el-main td {
    line-height: 40px;
  }
  .changepswInput {
    display: block;
    margin-left: 72px;
  }
</style>